.pageContainer {
  margin-left: 90px;
  margin-left: auto;
  margin-right: auto;
  align-items: center;
  justify-content: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, 400px);
  background-color: rgb(255, 255, 255);
  width: 1200px;
}

.pageItem {
  width: 300px;
  height: 380px;
  margin: 20px;
  padding: 30px; 
  background-color: rgb(255, 255, 255);
  text-align: center;
  box-shadow: 0 0 15px 5px rgba(68, 0, 255, 0.05);
  border-radius: 5px;
}

img {
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 15px;
  max-width: 200px;
  max-height: 200px;
}

